.buttons {
  display: flex;
  align-items: center;
  a {
    text-decoration: none;
    color: white;
  }
  img {
    width: 25px;
    height: 25px;
  }
  margin-top: -5px;
  @media (max-width: 450px) {
    img {
      width: 20px;
      height: 20px;
    }
  }
}

.linkedin-button {
  background-color: #1f1f1f;
  padding: 5px;
  display: flex;
  align-items: center;
  border-radius: 20px;
  margin-right: 15px ;
  @media (max-width: 450px) {
    font-size: small;
  }
}

.rootme-button {
  background-color: #1f1f1f;
  padding: 5px;
  display: flex;
  align-items: center;
  border-radius: 20px;

}

.github-button {
  background-color: #1f1f1f;
  padding: 5px;
  display: flex;
  align-items: center;
  border-radius: 20px;
  margin-right: 15px ;
}


